/* Login and Signup Form in React.js */
.container {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: auto;
    background: #fff;
    padding-bottom: 30px;
  }
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;
  }
  .text {
    color: #d3435b;
    font-size: 48px;
    font-weight: 700;
  }
  
  .underline {
    width: 61px;
    height: 6px;
    background: #d3435b;
    border-radius: 9px;
  }
  .inputs {
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
  
  .input {
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background: #eaeaea;
    border-radius: 6px;
  }
  .input img {
    margin: 0px 30px;
  }
  
  .input input {
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    font-size: 19px;
  }
          
  .forgot-password {
    padding-left: 35%;
    margin-top: 27px;
    color: #797979;
    font-size: 18px;
  }
  
  .forgot-password span {
    color: #d3435b;
    cursor: pointer;
  }
  .submit-container {
    display: flex;
    gap: 30px;
    margin: 60px auto;
  }
  
  .submit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 220px;
    height: 59px;
    color: #fff;
    background: #d3435b;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
  }
  .gray {
    background: #EAEAEA;
    color: #676767;
  }